.card-enter {
    opacity: 0;
    transform: scale(.5);
}
.card-enter-active {
    opacity: 1;
    transform: scale(1);
    transition: all 400ms;
}
.card-exit {
    opacity: 1;
    transform: scale(1);
}
.card-exit-active {
    opacity: 0;
    transform: scale(.5);
    transition: all 400ms;
}